@import url(https://fonts.googleapis.com/css?family=Archivo+Black&display=swap);
@import url(https://fonts.googleapis.com/css?family=Archivo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:'Archivo', sans-serif}#wrapper-light{background-color:#fff}#wrapper-dark{background-color:#1B1B1D}.main{max-width:1024px;margin:0 auto}.enter{font-family:'Archivo', sans-serif;height:100vh;width:100%;display:flex;justify-content:center;align-items:center;font-weight:600;padding-bottom:40vh}.enter-light{color:#1B1B1D;text-shadow:0px 0px 1px black}.enter-dark{color:#fff}a{all:inherit}a:hover{all:inherit}.navbar{position:fixed;overflow:hidden;width:100%;height:60px;top:0;box-shadow:0px 0px 10px 0px rgba(0,0,0,0.75);display:flex;text-align:center;font-weight:700;font-size:30px;z-index:10}#nav-light{background-color:#fff;color:#1B1B1D}#nav-dark{background-color:#1B1B1D;color:#fff}.nav-links{display:flex;justify-content:center;text-decoration:none;font-size:16px;margin-left:20px;padding-right:5px}#nav-link:hover{color:#2FC48D;cursor:pointer}header{font-family:'Archivo Black', sans-serif;display:block;float:left;text-align:center;margin-left:10px}header:hover{color:#2FC48D;cursor:pointer}.icon{display:none}.form-container{display:flex;justify-content:center;align-items:center;margin-left:auto;margin-right:auto;padding-left:60px}input[type="text"]{background-color:transparent;border:0;border-bottom:solid 2px #2FC48D;width:240px;padding-bottom:4px;font-weight:lighter;margin-right:10px;font-size:20px;outline:none}#input-mode-light{color:#1B1B1D !important}#input-mode-dark{color:#fff !important}button{border:0;padding:8px 20px;margin:0 2px;border-radius:10px;font-weight:lighter;letter-spacing:1px;font-size:15px;cursor:pointer;background-color:#2fc48d;color:#fff;font-weight:700}button:hover{font-weight:bold;box-shadow:0px 0px 5px 0px #2FC48D}.price-bar{position:-webkit-sticky;position:sticky;top:60;width:100%;padding-bottom:5px;padding-top:10px;z-index:2;display:block;text-align:center;margin-left:auto;margin-right:auto;margin-top:70px}.tickerMain{font-family:'Archivo Black', sans-serif;font-size:50px;font-weight:700}#tickerMain-light{color:#1B1B1D}#tickerMain-