@import url('https://fonts.googleapis.com/css?family=Archivo+Black&display=swap');
@import url('https://fonts.googleapis.com/css?family=Archivo&display=swap');

$text-dark: white;
$text-light: #1B1B1D;
$text-positive: #2FC48D;
$text-negative: #e54012;
$background-dark: #1B1B1D;
$background-light: white;


body {
  font-family: 'Archivo', sans-serif;
}

#wrapper-light {
  background-color: $background-light;
}

#wrapper-dark {
  background-color: $background-dark;
}

.main {
  max-width: 1024px;
  margin: 0 auto;
} 

.enter {
  font-family: 'Archivo', sans-serif;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  padding-bottom: 40vh;
}

.enter-light {
  color: $text-light;
  text-shadow: 0px 0px 1px black;
}

.enter-dark {
  color: $text-dark
}

//Nav
a {
  all: inherit;
}

a:hover {
  all: inherit;
}

.navbar {
  position: fixed;
  overflow: hidden;
  width: 100%;
  height: 60px;
  top: 0;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.75);
  display: flex;
  text-align: center;
  font-weight: 700;
  font-size: 30px;
  z-index: 10;
}

#nav-light {
  background-color: $background-light;
  color: $text-light;
}

#nav-dark {
  background-color: $background-dark;
  color: $text-dark;
}

.nav-links {
  display: flex;
  justify-content: center;
  text-decoration: none;
  font-size: 16px;
  margin-left: 20px;
  padding-right: 5px;
}

#nav-link:hover {
  color: #2FC48D;
  cursor: pointer;
}

header {
  font-family: 'Archivo Black', sans-serif;
  display: block;
  float: left;
  text-align: center;
  margin-left: 10px;
}

header:hover {
  color: #2FC48D;
  cursor: pointer;
}

.icon {
  display: none;
}


//Nav Form
.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-left: 60px;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 2px #2FC48D;
  width: 240px;
  padding-bottom: 4px;
  font-weight: lighter;
  margin-right: 10px;
  font-size: 20px;
  outline: none;
}

#input-mode-light {
  color: $text-light !important;
}

#input-mode-dark {
  color: $text-dark !important;
}


button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 10px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #2fc48d;
  color: #fff;
  font-weight: 700;
}

button:hover {
  font-weight: bold;
  box-shadow: 0px 0px 5px 0px $text-positive;
}


.price-bar {
  position: -webkit-sticky;
  position: sticky;
  top: 60;
  width: 100%;
  padding-bottom: 5px;
  padding-top: 10px;
  z-index: 2;
  display: block;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 70px;
}

.tickerMain {
  font-family: 'Archivo Black', sans-serif;
  font-size: 50px;
  font-weight: 700;
}

#tickerMain-light {
  color: $text-light
}

#tickerMain-dark {
  color: $text-dark
}

.stock_price_postiive {
  color: #2FC48D;
  font-size: 50px;
  font-weight: 600;
  margin-top: -10px;
  margin-bottom: 20px
}

.stock_price_negative {
  color: #e54012;
  font-size: 50px;
  font-weight: 600;
  margin-top: -10px;
  margin-bottom: 20px
}

.smallpos {
  color: #2FC48D
}

.smallneg {
  color: #e54012
}

.todayPrice {
  margin-top: -20px;
  margin-bottom: 40px;
  font-size: 14px;
  font-weight: 700;
}

#todayPrice-light {
  color: $text-light
}

#todayPrice-dark {
  color: $text-dark
}

//Chart
.chart-widget {
  width: 100%;
  height: 350px;
  margin-bottom: 40px;
  display: flex;
  justify-content: center;
}


//Company Information
.stock-key {
  border-bottom: solid 2px rgba(255,255,255,0.06);
  padding: 1px 0 1px 0;
  font-weight: 400;
}

#stock-key-light {
  color: $text-light
}

#stock-key-dark {
  color: $text-dark
}


//News Section
.news {
  margin-top: 20px;
  margin-bottom: 20px;
}

#news-light {
  color: $text-light
}

#news-dark {
  color: $text-dark
}

.section-title {
  font-family: 'Archivo Black', sans-serif;
  font-weight: 700;
}

#section-title-light {
  color: $text-light
}

#section-title-dark {
  color: $text-dark
}

.news-container {
  width: auto;
}

.article-container {
  padding: 5px;
  border-top: solid 1px rgba(0, 0, 0, 0.178);
  border-bottom: solid 1px rgba(0, 0, 0, 0.178);
  border-radius: 10px;
  width: 100%;
  height: 100%;
  max-height: 230px;
  overflow: hidden;
}

.article-container:hover {
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 10px;
  cursor: pointer;
}

#article-container-light:hover {
  border-top: solid 3px rgba(0, 0, 0, 0.178);
  border-bottom: solid 3px rgba(0, 0, 0, 0.178);
}

#article-container-dark:hover {
  background-color: #0E0E0F;
  border-top: solid 2px rgba(255,255,255,0.06);
  border-bottom: solid 2px rgba(255,255,255,0.06);
}

.article-source {
  font-weight: 800;
  font-size: 20px;
  margin-bottom: 2px;
}

.article-title {
  font-weight: 700;
}

.article-info {
  display: flex;
  font-size: 12px;
  color: #668087;
  overflow: hidden;
}

.article {
  overflow: hidden;
}

//Footer
.ticker-tape-light {
  display: flex;
  justify-content: center;
  position: fixed;
  left: 0;
  bottom: 0;
  padding-bottom: 10px;
  padding-right: 40px;
  width: 100%;
  background-color: $background-light
}

.ticker-tape-dark {
  display: flex;
  justify-content: center;
  position: fixed;
  left: 0;
  bottom: 0;
  padding-bottom: 10px;
  padding-right: 40px;
  width: 100%;
  background-color: $background-dark
}


//Mode Select
.checkbox-container {
  display: flex;
  justify-content: flex-end;
  position: fixed;
  left: 0;
  bottom: 0;
  margin-bottom: 13px;
  padding-right: 35px;
  width: 100%;
  z-index: 99;
}

input[type="checkbox"]{
  display: none;
}

label {
	cursor: pointer;
	width: 100px;
	height: 25px;
	background: $background-light;
	display: block;
	border-radius: 100px;
  position: relative;
  box-shadow: 0px 0px 3px  $background-dark;

  .toggle-text-light {
    display: none;
  }
}

label:after {
  content: '';
  position: absolute;
  top: 2.5px;
  left: 2.5px;
  width: 20px;
  height: 20px;
  background: $background-dark;
  border-radius: 50%;
  transition: 0.3s;
}

input:checked + label {
  background: $background-dark;
  border: 2px solid $background-light;
  

  .toggle-text-dark {
    display: none;
  }

  .toggle-image-dark {
    display: none;
  }

  .toggle-text-light {
    display: flex;
    align-items: center;
    margin-top: -1px;
  }
}

input:checked + label:after {
  height: 19px;
  width: 19px;
  margin-top: -1.5px;
	left: calc(100% - 5px);
  transform: translateX(-100%);
  background: $background-light;
}

label:active:after {
	width: 30px;
}

.toggle-text {
  display: flex;
  justify-content: center;
  align-items: center;
  color: $text-dark;
}

.toggle-text-dark {
  color: $text-light
}

.toggle-image-dark {
  height: 18px;
  width: 18px;
  background-color: $background-light;
  position: absolute;
  top: 4px;
  left: 4px;
  z-index: 100;
  clip-path: inset(0 50% 0 0 round 50% 0% 0% 50%)
}

.mode-switch-container-mobile {
  display: none;
}


//desktop - small and table
@media only screen and (max-width: 800px) {

  //Nav
  .nav-links {
    display: none;
  }

  //Nav Form
  .form-container {
    padding-left: 10px;
  }
  
  input[type="text"] {
    width: 200px;
  }

  //Footer
  .ticker-tape-light {
    display: flex;
    justify-content: center;
    position: fixed;
    left: 0;
    bottom: 0;
    padding-bottom: 10px;
    padding-right: 0px;
    width: 100%;
    background-color: $background-light
  }

  .ticker-tape-dark {
    display: flex;
    justify-content: center;
    position: fixed;
    left: 0;
    bottom: 0;
    padding-bottom: 5px;
    padding-right: 0px;
    width: 100%;
    background-color: $background-dark
  }


  //Mode Selector
  .checkbox-container {
    display: flex;
    justify-content: flex-end;
    position: fixed;
    left: 0;
    bottom: 0;
    margin-bottom: 8px;
    padding-right: 0px;
    width: 100%;
    z-index: 99;
  }

  .mode-switch-container-desktop {
    display: none;
    visibility: 0;
  }

  .mode-switch-container-mobile {
    display: block;
  }

  .toggle-image {
    display: none;
  }
  
  label {
    cursor: pointer;
    width: 60px;
    height: 60px;
    background: $background-light;
    display: block;
    border-radius: 5px;
    position: relative;
    border-left: 2px solid #dbdbdb;
    box-shadow: 0px 0px 0px  $background-dark;
  }

  #img-sun {
    width: 50px;
    height: 50px;
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 1000;
    margin-right: 5px;
    margin-bottom: 5px;
  }

  #img-moon {
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 1000;
    margin-right: 12px;
    margin-bottom:14px;
    filter: invert(100%);
    display: none;
  }

  label:after {
    display: none;
  }
  
  input:checked + label {
    background: $background-dark;
    border: none;
    border-left: 3px solid #dbdbdb;
    border-right: 3px solid $background-dark;
    box-shadow: 2px 0px 5px  $background-dark;
    
    #img-sun {
      display: none;
    }

    #img-moon {
      display: block;
    }
    
  }

}

//mobile
@media only screen and (max-width: 475px) {

  //Navbar
  .navbar {
    height: 50px;
  }
  
  header {
    display: none;
  }
  
  .nav-links {
    display: none;
  }

  header {
    display: none;
  }

  .icon {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    margin-top: 4px;
    margin-left: 4px;    
    float: left;
    width: 50px;
    height: 50px;
    box-shadow: none;
    clip-path: inset(25% 25% 25% 25%)
  }

  #icon-img-light {
    width: 40px;
    height: 40px;
  }

  #icon-img-dark {
    width: 40px;
    height: 40px;
    filter: invert(100%)
  }


  //Nav Form
  .form-container {
    display: flex;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    margin-top: 6.5px;
    margin-right: 15px;
  }

  input[type="text"] {
    min-width: 60vw;
    max-width: 80vw;
    width: 100%;
    font-size: 16px;
  }

  button {
    padding: 8px 15px;
  }

  // Price Bar

  .price-bar {
    width: 100%;
    padding-bottom: 0px;
    margin-bottom: 0px;
    z-index: 2;
    display: block;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    padding-top: 10px;
  }

  .article-container {
    padding-left: 0px;
    padding-right: 0px
  }



  //Mode Selector
  .checkbox-container {
    display: flex;
    justify-content: flex-end;
    position: fixed;
    left: 0;
    bottom: 0;
    margin-bottom: 8px;
    padding-right: 0px;
    width: 100%;
    z-index: 99;
  }

  .mode-switch-container-desktop {
    display: none;
    visibility: 0;
  }

  .mode-switch-container-mobile {
    display: block;
  }

  .toggle-image {
    display: none;
  }
  
  label {
    cursor: pointer;
    width: 60px;
    height: 60px;
    background: $background-light;
    display: block;
    border-radius: 5px;
    position: relative;
    border-left: 2px solid #dbdbdb;
    box-shadow: 0px 0px 0px  $background-dark;
  }

  #img-sun {
    width: 50px;
    height: 50px;
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 1000;
    margin-right: 5px;
    margin-bottom: 5px;
  }

  #img-moon {
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 1000;
    margin-right: 12px;
    margin-bottom:14px;
    filter: invert(100%);
    display: none;
  }

  label:after {
    display: none;
  }
  
  input:checked + label {
    background: $background-dark;
    border: none;
    border-left: 3px solid #dbdbdb;
    border-right: 3px solid $background-dark;
    box-shadow: 2px 0px 5px  $background-dark;
    
    #img-sun {
      display: none;
    }

    #img-moon {
      display: block;
    }
    
  }
  
}